import { Plugin } from '@ali/4ever-cangjie';

type RunSequence<P> = {
  runAfter?: Array<Factory<P>>;
};

export type Factory<P> = ((...args: any) => P) & RunSequence<P>;

type PluginItem<P> = {
  key: string;
  factory: Factory<P>;
};

function findAllDeps<P>(runAfter?: Array<Factory<P>>) {
  const array = runAfter || [];
  const children = [...array];
  while (children.length) {
    const child = children.pop();
    const deps = child?.runAfter || [];
    deps.forEach((d) => {
      if (!array.some((a) => a === d)) {
        array.push(d);
        children.push(d);
      }
    });
  }
  return array;
}

export function sortPlugins<P = Plugin>(
  pluginsFactoryMap: Record<string, Factory<P>>,
) {
  const items: Array<PluginItem<P>> = [];
  const factories: Function[] = [];
  if (!pluginsFactoryMap) {
    return items;
  }
  Object.keys(pluginsFactoryMap).forEach((key) => {
    const factory = pluginsFactoryMap[key];
    if (!factory) {
      console.error(`plugin ${key} is undefined`);
      return;
    }
    factories.unshift(factory);
    items.push({ key, factory });
  });

  factories.forEach((f) => {
    const itemIndex = items.findIndex(({ factory }) => factory === f);
    const item = items[itemIndex];
    if (item?.factory.runAfter?.length) {
      const allDeps = findAllDeps(item.factory.runAfter);
      const depsIndexes = allDeps.map((dep) =>
        items.findIndex((i) => i.factory === dep),
      );
      const maxIndex = Math.max(...depsIndexes);
      if (maxIndex > itemIndex) {
        items.splice(itemIndex, 1);
        items.splice(maxIndex, 0, item);
      }
    }
  });

  return items;
}
